<template>
<div>
    <p>userinfo</p>
    <!--<p>{{authUser.access_token}}</p>-->
  <router-link to="Login"><el-button>Login</el-button></router-link>
  <p>{{authUser.full_name}}</p>
  <p>{{authUser.email}}</p>
</div>

</template>
<script>
    import ls from 'localStorage'
    import ElButton from "../../node_modules/element-ui/packages/button/src/button.vue";

    export default {
      components: {ElButton},
      props:{

        },
        data(){
            const authUser = JSON.parse(ls.getItem("authUser"));
            console.log(ls.getItem("authUser"));
            console.log('parse',authUser)
            return {authUser};
        }
    }
</script>



